import Vue from "vue";
import Router from "vue-router";
import UserList from "./views/UserList.vue";
import UserDetails from "./views/UserDetails.vue";
import ShowMap from "./views/ShowMap.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "user-list",
      component: UserList
    },
    {
      path: "/udetails/:user",
      name: "user-details", // or your preferred name
      props: true,
      component: UserDetails
    },
    {
      path: "/umap/:user",
      name: "show-map", // or your preferred name
      props: true,
      component: ShowMap
    }
  ]
});
