<template>
  <div>
    <button v-on:click="$router.go(-1)">Back</button>
    <h2>Details of {{user.name.title}}. {{user.name.first}} {{user.name.last}}</h2>

    <table>
      <tr>
        <th>Name</th>
        <th>Phone Numbers</th>
        <th>Address</th>
        <th>Date of Birth</th>
        <th>Photo</th>
      </tr>
      <tr>
        <td>{{ user.name.first }} {{ user.name.last }}</td>
        <td>
          Phone: {{ user.phone }}
          <br>
          Cell: {{ user.phone }}
        </td>
        <td v-on:click="showMap(user)">
          {{ user.location.street }}
          <br>
          {{ user.location.city }}, {{ user.location.state }} {{ user.location.postcode }}
        </td>
        <td>{{ user.dob.date.substring(0, 10) }}</td>
        <td>
          <img v-bind:src="`${user.picture.thumbnail}`">
        </td>
      </tr>
    </table>

    <gmap-map :center="center" :zoom="5" style="width:100%;  height: 400px;">
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        @click="center=m.position"
      ></gmap-marker>
    </gmap-map>

    <!--  <p>Raw data 1: {{user}}</p> -->
  </div>
</template>
 <script>
import GoogleMap from "./GoogleMaps";

export default {
  data() {
    return {
      center: { lat: 45.508, lng: -73.587 },
      markers: [],
      places: [],
      currentPlace: null
    };
  },
  props: {
    user: {
      type: Object // user prop is a JS object
    }
  },
  components: {
    GoogleMap
  },
  mounted() {
    // console.log(this.center.lat + ", " + this.center.lng);
    // console.log(this.$route.params.user.location.coordinates.latitude);
    // console.log(this.$route.params.user.location.coordinates.longitude);
    this.center.lat = parseFloat(
      this.$route.params.user.location.coordinates.latitude
    );
    this.center.lng = parseFloat(
      this.$route.params.user.location.coordinates.longitude
    );

    const marker = {
      lat: (this.center.lat = parseFloat(
        this.$route.params.user.location.coordinates.latitude
      )),
      lng: (this.center.lng = parseFloat(
        this.$route.params.user.location.coordinates.longitude
      ))
    };
    this.markers.push({ position: marker });
  },
  methods: {
    // receives a place object via the autocomplete component
    setPlace(place) {
      this.currentPlace = place;
    },
    addMarker() {
      if (this.currentPlace) {
        console.log("adding marker");
        const marker = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        };
        this.markers.push({ position: marker });
        this.places.push(this.currentPlace);
        this.center = marker;
        this.currentPlace = null;
      }
    },
    showMap(user) {
      this.$router.push({
        name: "show-map", // or your preferred name
        params: {
          user: user
        }
      });
    }
  }
};
</script>
