<template>
  <div id="top">
    <h2>Vue SFC and Vue Router Tutorial by Trevor West</h2>
    <div id="control">
      <input type="range" id="inputrange" min="1" max="10" v-model="userCount">
      <button v-on:click="getUser">Fetch</button>
      <button v-on:click="resetUser">Reset</button>
    </div>

    <table v-show="(users != undefined && users.length > 0)">
      <tr>
        <th>Name</th>
        <th>Email</th>
        <th>Action</th>
      </tr>
      <template v-for="(u,pos) in users">
        <tr v-bind:key="pos">
          <!-- complete this -->
          <td>{{ u.name.first }} {{ u.name.last }}</td>
          <td>{{ u.email }}</td>
          <td>
            <button v-on:click="showDetails(u)">Details</button>
          </td>
        </tr>
      </template>
    </table>
  </div>
</template>

 <script>
var myGlobal = {};

export default {
  data() {
    // Notice that data is now a FUNCTION
    return {
      userCount: 4,
      users: []
    };
  },
  mounted() {
    // THIS IS THE LIFECYCLE METHOD!
    this.users = myGlobal.users;
  },
  methods: {
    getUser() {
      // copy the code from the previous tutorial
      fetch(
        `https://randomuser.me/api?results=${this.userCount}&nat=${
          this.nationalities
        }`
      )
        .then(r => r.json()) // convert the response 'r' to a JSON object
        .then(u => {
          // This will show a JSON string inside the <div> defined above
          this.users = u.results;
          // keep a copy in our global storage
          myGlobal.users = u.results;
        });
    },
    resetUser() {
      // copy the code from the previous tutorial
      this.users = [];
      myGlobal.users = [];
    },
    showDetails(who) {
      //alert(who.name.first);
      this.$router.push({
        name: "user-details", // or your preferred name
        params: {
          user: who
        }
      });
    }
  }
};
</script>